	function trigStdSliders() {

		$('.rwp-slider-std').each(function(i){

			$(this).noUiSlider({
	            step: 1,
	            start: $(this).data('val'),
	            connect: "lower",
	            range: {
	              'min': $(this).data('min'),
	              'max': $(this).data('max')
	            },
	            format: wNumb({
	                decimals: 0,
	            })
	        });

	        $(this).Link('lower').to($(this).parent().find('input'), null, wNumb({decimals: 0 }));


	    });

	}

	function trigLimitsSliders() {

		$('.rwp-slider-limits').each(function(i){

			var mi = $(this).data('min');
			var ma = $(this).data('max');

			ma = (ma == 'inf') ? 10000 : ma;
			
			$(this).noUiSlider({
				connect: true,
				step: 10,
				start: [ mi, ma ],
				range: {
					// Starting at 500, step the value by 500,
					// until 4000 is reached. From there, step by 1000.
					'min': [ 0 ],
					'90%': 5000,
					'max': 10000
				},
				format: wNumb({
	                decimals: 0
	            })
			});

	        $(this).Link('lower').to($(this).parent().find('input.rwp-min'), null, wNumb({decimals: 0 }));
	        $(this).Link('upper').to($(this).parent().find('input.rwp-max'), null, wNumb({decimals: 0 }));

	        $(this).on({
				slide: function(){

					var min = $(this).val()[0];
					var max = $(this).val()[1];
					
					if( max > 5000 ) {
						$(this).parent().find('input.rwp-max').val('no-limit');
					}
				}
			});

	        if( ma > 5000 ) {
				$(this).parent().find('input.rwp-max').val('no-limit');
			}

	    });

	}


